<template>
  <!-- 服务信息 -->
  <div class="container">
    <el-tabs v-model="activeName">
      <el-tab-pane label="服务信息" name="serviceInformation">
        <serviceInformationDetails
          ref="serviceInformationDetails"
        ></serviceInformationDetails>
      </el-tab-pane>
    </el-tabs>
    <div class="loginOut">
      <el-form-item>
        <el-button type="primary" size="mini" @click="saveBtn()"
          >保存</el-button
        >
        <el-button size="mini" @click="returnBtn()">返回</el-button>
      </el-form-item>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs } from "vue";
import serviceInformationDetails from "../service_management/serviceInformationDetails.vue";
import { getCurrentInstance } from "@vue/runtime-core";
export default {
  components: {
    serviceInformationDetails,
  },
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      activeName: "serviceInformation",
    });
    const saveBtn = () => {
      proxy.$router.push("/service");
    };
    const returnBtn = () => {
      // proxy.$router.push("/service");
      proxy.$router.go(-1)
    };

    return {
      ...toRefs(state),
      saveBtn,
      returnBtn,
    };
  },
};
</script>

<style scoped>
.container {
  background-color: #fff;
  position: relative;
}

.loginOut {
  position: absolute;
  right: 10px;
  top: 0;
}
</style>